<template>
  <table class="w-full">
    <table-header :fields="[
        {
          label: 'ID',
          sortBy: 'id',
          class: 'p-4 pl-10 text-left'
        }, {
          label: 'Height',
          sortBy: 'height',
          class: 'p-4 text-left hidden md:table-cell'
        }, {
          label: 'Timestamp',
          sortBy: 'timestamp',
          class: 'p-4 text-left'
        }, {
          label: 'Transactions',
          sortBy: 'numberOfTransactions',
          class: 'p-4 text-right hidden md:table-cell'
        }, {
          label: 'Generated by',
          sortBy: 'generatorPublicKey',
          class: 'p-4 text-right hidden lg:table-cell'
        }, {
          label: 'Reward (ARK)',
          sortBy: 'totalForged',
          class: 'p-4 text-right'
        }, {
          label: 'Fee (ARK)',
          sortBy: 'totalFee',
          class: 'p-4 pr-10 text-right'
        },
    ]" :sort-key="sortKey" :sort-direction="sortDirection" :sort-symbol="sortSymbol" :handler="sortBy"></table-header>
    <tbody>
      <tr v-for="block in sortedBlocks" :key="block.id">
        <td class="p-4 pl-10 text-left border-none">
          <block-link :id="block.id"></block-link>
        </td>

        <td class="p-4 text-left border-none hidden md:table-cell">
          {{ block.height }}
        </td>

        <td class="p-4 text-left border-none">
          {{ readableTimestamp(block.timestamp) }}
        </td>

        <td class="p-4 text-right hidden md:table-cell border-none">
          {{ block.numberOfTransactions }}
        </td>

        <td class="p-4 text-right border-none hidden lg:table-cell">
          <wallet-link :public-key="block.generatorPublicKey"></wallet-link>
        </td>

        <td class="p-4 text-right border-none">
          {{ readableCrypto(block.totalForged) }}
        </td>

        <td class="p-4 pr-10 text-right border-none">
          {{ readableCrypto(block.totalFee) }}
        </td>
      </tr>
    </tbody>
  </table>
</template>

<script type="text/ecmascript-6">
import Currency from '@/components/utils/Currency'
import WalletLink from '@/components/links/Wallet'
import BlockLink from '@/components/links/Block'
import TableHeader from '@/components/table/TableHeader'
import SortableTable from '@/mixins/sortable-table'

export default {
  mixins: [SortableTable],

  components: { Currency, BlockLink, WalletLink, TableHeader },

  props: {
    blocks: {
      type: Array,
      required: true,
    },
  },

  data: () => ({ sortKey: 'timestamp' }),

  computed: {
    sortedBlocks() {
      return _.orderBy(this.blocks, this.sortKey, this.sortDirection)
    }
  }
}
</script>

<style scoped>
tbody tr:nth-child(even) {
  background-color: var(--color-theme-table-row);
}

tbody tr:hover {
  background-color: rgba(0, 0, 0, 0);
  transform: scale(1.015);
  box-shadow: var(--theme-table-hover-shadow);
  z-index: 999;
  border-radius: 7px;
}
tr:hover,
tr:hover td {
  background-color: var(--color-theme-table-row-hover);
}
tr:hover td:first-child {
  border-top-left-radius: 7px;
  border-bottom-left-radius: 7px;
}
tr:hover td:last-child {
  border-top-right-radius: 7px;
  border-bottom-right-radius: 7px;
}
</style>



